import { Component, Input, Output, EventEmitter, ViewChild, forwardRef, Renderer, ElementRef } from '@angular/core';
import { ControlValueAccessor, FormControlName, NG_VALUE_ACCESSOR } from '@angular/forms';
//
import { BaseControl } from './base-control';
import { BaseComponent } from '../base-component';
import { Policies } from '../security/policies';

@Component({
    moduleId: module.id,
    selector: 'az-combobox',
    templateUrl: './az-combobox.component.html',
    providers: [
        {
            provide: BaseComponent, useExisting: forwardRef(() => AzComboboxComponent)
        },
        {
            provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => AzComboboxComponent), multi: true
        }
    ]
})
export class AzComboboxComponent extends BaseControl implements ControlValueAccessor {
    @Input() items: any[] = [];
    @Input() displayMember: string = '';
    @Input() valueMember: string = '';
    @Input() value: string;
    @Input() selectedItem: any = null;
    //
    @Output() selectedItemChanged: EventEmitter<any> = new EventEmitter<any>();

    onChange(args: any) {
        this.selectedItemChanged.next(this.selectedItem);
        this.propagateChange(this.value);
    }

    //ImplementaciÃ³n de control value accesor
    writeValue(value: any) {
        this.value = value ? value : '';
    }

    propagateChange = (_: any) => { };

    registerOnChange(fn: any) {
        this.propagateChange = fn;
    }

    registerOnTouched() { }

}